<template>
  <div id="supplier-detail-component" v-if="detail">
    <div class="top-container">
      <img
        class="brand-image"
        :src="detail.logo.file"
        alt=""
        @click="onBrandClick"
      />
      <div class="content-container">
        <h1 class="title">{{ detail.name }}</h1>
        <p class="description">
          {{ detail.company_home_page.about_us }}
        </p>
      </div>
    </div>

    <div class="video-container" v-if="detail.intro_video">
      <video :src="detail.intro_video.file" height="500" controls></video>
    </div>

    <div class="fabrics-container">
      <h3 class="title">LittleKing</h3>
      <el-row :gutter="40">
        <el-col v-for="index in 6" :key="index" :span="8">
          <FabricCard></FabricCard>
        </el-col>
      </el-row>
    </div>

    <div class="button-container">
      <el-button type="success" @click="onContactUsClick()">
        Contact us
      </el-button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";

import FabricCard from "@/components/FabricCard.vue";
import { GetCompany } from "@/typings/store";
import { CompanyResponse } from "@/typings/company";

const Component = defineComponent({
  name: "SupplierDetail",
  components: {
    FabricCard
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const store = useStore();
    const { id } = toRefs(props);

    const detail = computed<CompanyResponse>(() => store.state.company.detail);

    onMounted(async () => {
      await store.dispatch(GetCompany, id.value);
    });

    const onBrandClick = () => {
      window.open(detail.value.company_home_page.official_website, "blank");
    };

    const onContactUsClick = () => {
      console.log("contact us clicked");
    };

    return {
      detail,

      onContactUsClick,
      onBrandClick
    };
  }
});

export default Component;
</script>

